.trip_card_wrapper {
  display: flex;
  flex-direction: column;
  width: 200px;
  border-radius: 5px;
  border: 1px solid var(--Light-grey);
  cursor: pointer;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 140px;
}

.not_found_text {
  color: var(--Medium-grey);
  font-weight: normal;
}
