.modal_wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.modal_content {
  padding: 32px;
  background-color: var(--White);
  border-radius: 8px;
  width: 480px;
}
